
          @import '@/../styles/_variables.scss';
          @import '@/../styles/_mixins.scss';
        

.example-container {
  position: relative;

  padding: 30px 20px;

  border: solid 1px $color-dark-10;

  margin: 40px 0;

  &__title {
    $height: 20px;

    position: absolute;
    top: -$height / 2;
    left: 10px;

    padding: 0 10px;
    background: $color-light;
    margin: 0;

    color: $color-dark-60;
    font-size: 14px;
    line-height: $height;
    font-weight: normal;

    // text-transform: uppercase;
  }
}
